	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>Dear Sue, just a little note to say a big thank you for all the hard work you put in re-vamping our house. We are really delighted with the final result.</p>\n<p>All the colours, soft furnishings and accessories work beautifully. The finished project has exceeded our expectations and our son’s girlfriend commented that our home should be in one of the Home Design Magazines!</p>\n<p><a href=\"/suedanninteriors.com/case-study/2012-victorian-country-house\">Case Study: Victorian Country House in Rode, Somerset</a></p>","frontmatter":{"name":"Janet Singleton and Richard Moffatt","address":"Rode, Somerset"}}},{"node":{"html":"<p>Sue Dann has worked with us on several projects, which involved designing fairly complex public areas in our Hotels. We have found her to be very enthusiastic, and her work has been inspiring. We have also found her to be able to work within budgetary confines, a rare ability in the designer world! Her touch of style and class is very evident in her schemes.</p>\n<p>Sue always has a professional and commercial approach to any project, and her ability to source specific items is a great asset. She has a wealth of knowledge, and her design experience is very apparent. She takes real personal interest in her projects, and we heartily recommend her to any potential client.</p>\n<p><a href=\"/suedanninteriors.com/case-study/2008-georigan-apartment\">Case Study: Georgian Apartment in Sydney Place, Bath</a></p>","frontmatter":{"name":"Mr & Mrs Eve","address":"Sydney Place, Bath"}}},{"node":{"html":"<p>Sue Dann has worked with us on several projects, which involved designing fairly complex public areas in our Hotels. We have found her to be very enthusiastic, and her work has been inspiring. We have also found her to be able to work within budgetary confines, a rare ability in the designer world! Her touch of style and class is very evident in her schemes.</p>\n<p>Sue always has a professional and commercial approach to any project, and her ability to source specific items is a great asset. She has a wealth of knowledge, and her design experience is very apparent. She takes real personal interest in her projects, and we heartily recommend her to any potential client.</p>\n<p><a href=\"/suedanninteriors.com/commercial\">The Kings Arms Hotel, Old Amersham on our Commercial Design page</a></p>","frontmatter":{"name":"Nick Sonley, Operations & Development Director, Zola Hotels","address":"Central Office, 30 High Street, Old Amersham, Bucks, HP7 0DJ"}}},{"node":{"html":"<p>We have used Sue Dann Interiors twice now to advise us on the new design and refurbishment of our homes, and cannot recommend her highly enough. Her attention to detail, whether of design or on the financial side, was thorough and professional. She spent a lot of time familiarising herself with our own tastes and ideas, then provided us with a huge range of choice and alternatives, which resulted in beautiful and functional interiors.</p>\n<p><a href=\"/suedanninteriors.com/case-study/2011-period-cottage\">Case Study: Peroid Cottage near Bath</a></p>","frontmatter":{"name":"Mr & Mrs Woods","address":"Wellow, Somerset"}}}]}},"pathContext":{}}

